/*
* 主体部分
* 直接用react router 做了路由导航
*
* CustomersInfos 是客户信息部分
* PunchStatistics 是打卡统计部分
* RenewStatistics 是续费统计
* InviteStatistics 是邀请统计
*/
import React, {Component} from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import TitleSection from './TitleSection';
import CustomersInfos from './CustomersInfos';
import PunchStatistics from './PunchStatistics';
import RenewStatistics from './RenewStatistics';
import InviteStatistics from './InviteStatistics';
import style from './Container.css';

class Container extends React.Component{
	constructor(props) {
	  super(props);
	  this.state = {}
	}

	render(){
		return (
			<div>
				<TitleSection></TitleSection>
				<Router>
					<div>
						<ul>
			        <Route exact={true} path="/CIM" children={({ match }) => (<li className={match ? style.active : undefined}><Link to="/CIM">客户信息</Link></li>)}/>
			        <Route path="/CIM/punch" children={({ match }) => (<li className={match ? style.active : undefined}><Link to="/CIM/punch">打卡统计</Link></li>)}/>
			        <Route path="/CIM/renew" children={({ match }) => (<li className={match ? style.active : undefined}><Link to="/CIM/renew">续费统计</Link></li>)}/>
			        <Route path="/CIM/invite" children={({ match }) => (<li className={match ? style.active : undefined}><Link to="/CIM/invite">邀请统计</Link></li>)}/>
			      </ul>
			      <div className={style.container_body}><Route exact path="/CIM" component={CustomersInfos}/></div>
			      <div className={style.container_body}><Route exact path="/CIM/punch" component={PunchStatistics}/></div>
			      <div className={style.container_body}><Route exact path="/CIM/renew" component={RenewStatistics}/></div>
			      <div className={style.container_body}><Route exact path="/CIM/invite" component={InviteStatistics}/></div>
					</div>
				</Router>
			</div>
		)
	}
}

export default Container


