import React, {
	Component
} from 'react';
import ReactDOM from 'react-dom';
import style from './StatisticsTable.css';

class StatisticsTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			infoContent: [],
			nowPage: 0,
			allPage: 1
		}
	}
	componentWillMount() {
		const len = this.props.infoContent.arr.length;
		const nowPage = this.state.nowPage;
		const allPage = Math.ceil(len / 10);
		if (len > 10) {
			this.setState({
				infoContent: this.props.infoContent.arr.slice(nowPage * 10, (nowPage + 1) * 10),
				allPage: allPage
			})
		} else {
			this.setState({
				infoContent: this.props.infoContent.arr
			})
		}
	}
	pageUp = () => {
		/*上一页*/
		const lastPage = this.state.nowPage - 1;
		this.setState({
			infoContent: this.props.infoContent.arr.slice(lastPage * 10, (lastPage + 1) * 10),
			nowPage: lastPage
		})
	}
	pageDown = () => {
		/*下一页*/
		const nextPage = this.state.nowPage + 1;
		this.setState({
			infoContent: this.props.infoContent.arr.slice(nextPage * 10, (nextPage + 1) * 10),
			nowPage: nextPage
		})
	}
	render() {
		return (
			<div className={style.container}>
				<div className={style.table}>
					<TableTitle infoTitle1={this.props.infoTitle1} infoTitle2={this.props.infoTitle2}></TableTitle>
					<TableBody infoContent={this.state.infoContent}></TableBody>
					<TablePaging nowPage={this.state.nowPage} allPage={this.state.allPage} pageUp={this.pageUp} pageDown={this.pageDown}></TablePaging>
				</div>
			</div>
		)
	}
}


class TableTitle extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<div className={style.table_title}>
				<span>
					{this.props.infoTitle1}
				</span>
				<span>
					{this.props.infoTitle2}
				</span>
			</div>
		)
	}
}

class TableBody extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<table className={style.other_table}>
				<tbody>
					<CreateTHead value={this.props.infoContent[0]}></CreateTHead>
					{this.props.infoContent.map((value, index) => 
						<CreateTd value={value} key={index}></CreateTd>
					)}
				</tbody>
			</table>
		)
	}
}
class CreateTHead extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		const obj = this.props.value;
		const arr = [];
		for (var key in obj) {
			arr.push(key)
		}
		return (
			<tr>
				{arr.map((value, index) => 
					<th key={index}>
						{value}
					</th>
				)}
			</tr>
		)
	}
}
class CreateTd extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		const obj = this.props.value;
		const arr = [];
		for (var key in obj) {
			arr.push(obj[key])
		}
		return (
			<tr>
				{arr.map((value, index) => 
					<td key={index}>
						{value}
					</td>
				)}
			</tr>
		)
	}
}

class TablePaging extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<div className={style.table_page}>
				{this.props.nowPage + 1 <= 1 ? <button>上一页</button> : <button onClick={this.props.pageUp} className={style.active}>上一页</button>}
				<p>第<span>{this.props.nowPage+1}</span>页/共<span>{this.props.allPage}</span>页</p>
				{this.props.nowPage + 1 >= this.props.allPage ? <button>下一页</button> : <button onClick={this.props.pageDown} className={style.active}>下一页</button>}
			</div>
		)
	}
}

export default StatisticsTable


