/*
 * 页面主体之客户信息
 * 默认显示列表
 */
import React, {
	Component
} from 'react';
import StudentInfos from './StudentInfos';
import style from './CustomersInfos.css';

class CustomersInfos extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	}
	render() {
		return (
			<div>
				<StudentInfos></StudentInfos>
			</div>
		)
	}
}

export default CustomersInfos


