// 基本信息
class UserInfomation extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<div className={style.table}>
				<TableHead title={this.props.title} goHref="https://www.baidu.com"></TableHead>
				<table className={style.basic_info_table}>
				<tbody>
						<tr>
							<td>姓名</td>
							<td>{this.props.infoContent.name}</td>
							<td>性别</td>
							<td>{this.props.infoContent.sex}</td>
							<td>地区</td>
							<td>{this.props.infoContent.address}</td>
							<td>UID</td>
							<td>{this.props.infoContent.uid}</td>
						</tr>
						<tr>
							<td>年级</td>
							<td>{this.props.infoContent.grade}</td>
							<td>语文教材</td>
							<td>{this.props.infoContent.yuwen}</td>
							<td>数学教材</td>
							<td>{this.props.infoContent.shuxue}</td>
							<td>英语教材</td>
							<td>{this.props.infoContent.yingyu}</td>
						</tr>
						<tr>
							<td>手机号</td>
							<td>{this.props.infoContent.number}</td>
							<td>家长端</td>
							<td>{this.props.infoContent.parent}</td>
							<td>会员剩余天数</td>
							<td>{this.props.infoContent.lastdays}</td>
							<td>辅导老师</td>
							<td>{this.props.infoContent.teacher}</td>
						</tr>
					</tbody>
				</table>
			</div>
		)
	}
}
// 表格
class TableContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			infoContent: [],
			nowPage: 0,
			allPage: 1
		}
	}
	componentWillMount() {
		const len = this.props.infoContent.arr.length;
		const nowPage = this.state.nowPage;
		const allPage = Math.ceil(len / 10);
		if (len > 10) {
			this.setState({
				infoContent: this.props.infoContent.arr.slice(nowPage * 10, (nowPage + 1) * 10),
				allPage: allPage
			})
		} else {
			this.setState({
				infoContent: this.props.infoContent.arr
			})
		}
	}
	componentWillReceiveProps() {
		const len = this.props.infoContent.arr.length;
		const nowPage = this.state.nowPage;
		const allPage = Math.ceil(len / 10);
		if (len > 10) {
			this.setState({
				infoContent: this.props.infoContent.arr.slice(nowPage * 10, (nowPage + 1) * 10),
				allPage: allPage
			})
		} else {
			this.setState({
				infoContent: this.props.infoContent.arr
			})
		}
	}
	pageUp = () => {
		/*上一页*/
		const lastPage = this.state.nowPage - 1;
		this.setState({
			infoContent: this.props.infoContent.arr.slice(lastPage * 10, (lastPage + 1) * 10),
			nowPage: lastPage
		})
	}
	pageDown = () => {
		/*下一页*/
		const nextPage = this.state.nowPage + 1;
		this.setState({
			infoContent: this.props.infoContent.arr.slice(nextPage * 10, (nextPage + 1) * 10),
			nowPage: nextPage
		})
	}
	addNewTalk = () => {
		this.props.addNewTalk();
	}
	render() {
		return (
			<div className={style.table}>
				<TableHead title={this.props.title} message={this.props.infoContent.message} addTalk={this.props.addTalk ? true : false} addNewTalk={this.addNewTalk}></TableHead>
				<TableBody infoContent={this.state.infoContent}></TableBody>
				<TablePaging nowPage={this.state.nowPage} allPage={this.state.allPage} pageUp={this.pageUp} pageDown={this.pageDown}></TablePaging>
			</div>
		)
	}
}


class TableHead extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			addShow: false
		}
	}
	addWindowShow = () => {
		this.setState({
			addShow: true
		})
	}
	addWindowHide = () => {
		this.setState({
			addShow: false
		})
	}
	addNewTalk = () => {
		this.props.addNewTalk();
	}
	render() {
		return (
			<div className={style.table_head}>
				<span className={style.table_head_title}>{this.props.title}</span>
				<span className={style.message}>{this.props.message}</span>
				{this.props.goHref ? <a href={this.props.goHref} target="_blink">修改</a> : undefined}
				{this.props.addTalk ? <button onClick={this.addWindowShow}>添加</button> : undefined}
				{this.state.addShow ? <AddTalk addHide={this.addWindowHide} addNewTalk={this.addNewTalk}></AddTalk> : undefined}
			</div>
		)
	}
}

class TableBody extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<table className={style.other_table}>
				<tbody>
					{this.props.infoContent.map((value, index) => 
						<CreateTd value={value} key={index}></CreateTd>
					)}
				</tbody>
			</table>
		)
	}
}
class CreateTd extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		const obj = this.props.value;
		const arr = [];
		for (var key in obj) {
			arr.push(obj[key])
		}
		return (
			<tr>
				{arr.map((value, index) => 
					<td key={index}>
						{value}
					</td>
				)}
			</tr>
		)
	}
}

class TablePaging extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		return (
			<div className={style.table_page}>
				{this.props.nowPage + 1 <= 1 ? <button>上一页</button> : <button onClick={this.props.pageUp} className={style.active}>上一页</button>}
				<p>第<span>{this.props.nowPage+1}</span>页/共<span>{this.props.allPage}</span>页</p>
				{this.props.nowPage + 1 >= this.props.allPage ? <button>下一页</button> : <button onClick={this.props.pageDown} className={style.active}>下一页</button>}
			</div>
		)
	}
}

// 添加沟通记录
class AddTalk extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			newTalk: ''
		}
	}
	addHide = () => {
		this.props.addHide();
	}
	submitAdd = () => {
		goutong.arr.push({
			'time': new Date().toLocaleString(),
			'content': this.state.newTalk
		});
		this.props.addNewTalk();
		this.addHide();
	}
	textareaHandleChange = (event) => {
		this.setState({
			newTalk: event.target.value
		})
	}
	render() {
		return (
			<div className={style.add_module}>
				<header>
					添加沟通记录
					<button className={style.close_add_btn} onClick={this.addHide}>X</button>
				</header>
				<div className={style.add_body}>
					<textarea name="" id="" cols="" rows="" onChange={this.textareaHandleChange.bind(this)}></textarea>
					<button className={style.submit} onClick={this.submitAdd}>添加</button>
				</div>
			</div>
		)
	}
}

export default StudentInfos


