/*
* 页面顶部栏
* 包含退出登录功能
* 退出登录清楚本地cookie即可，之后服务器端有需求可能会 发送退出登录请求，收到成功的响应后再执行本地退出的操作
*
*/
import React, {Component} from 'react';
import Cookie from 'universal-cookie';

import style from './TitleSection.css'

const cookies = new Cookie();

class TitleSection extends React.Component{
	constructor(props) {
	  super(props);
	  this.state = {};
	}
	handleLogoutClick = () => {
		// 这里清除登录信息，然后跳转到登录页面（直接去掉“/”后面的参数即可，之后部署到服务器可能要从新改）
		cookies.remove('uid', {path: '/'});
		window.location.href = window.location.origin + '/CIM/';
	}
	render(){
		return (
			<div className={style.title_section}>
				<p>CIM客户信息管理系统</p>
				<button onClick={this.handleLogoutClick}>退出登录</button>
			</div>
		)
	}
}

export default TitleSection


