/*
* 页面主体之续费统计
* 
*/
import React, {Component} from 'react';
import StatisticsTable from './StatisticsTable';
import style from './RenewStatistics.css';

const infoContent = {
	'7天': '34%',
	'30天': '44%',
	'arr': [
		{
			'日期': '2018/1/1',
			'过期学员数': '300',
			'续费数': '200',
			'续费率': '66.67%',
			'续费类型比例': '月：33%，半年：17%，终身：20%'
		},
		{
			'日期': '2018/1/3',
			'过期学员数': '200',
			'续费数': '100',
			'续费率': '50%',
			'续费类型比例': '月：33%，半年：17%，终身：20%'
		}
	]
}

class RenewStatistics extends React.Component{
	constructor(props) {
	  super(props);
	  this.state = {
	  	title: ''
	  };
	}
	componentWillMount(){
		this.setState({
			title1: '近7天(不含今天)平均续费率:' + infoContent['7天'],
			title2: '近30天(不含今天)平均续费率:' + infoContent['30天']
		})
	}
	render(){
		return (
			<div>
				<StatisticsTable infoContent={infoContent} infoTitle1={this.state.title1} infoTitle2={this.state.title2}></StatisticsTable>
			</div>
		)
	}
}

export default RenewStatistics


