/*
 * 验证登录状态，判断显示登录模块还是信息模块
 * 未登录只加载显示登录组件
 * 登录状态保存在cookie
 * 
 * 关于css ，引入了css-loader和style-loader两个包。
 * 然后再组件中引入样式表的时候： import style from './xxx.css'
 * 在元素上写class时： <div className={style.xxx_xxx}>栗子</div>
 * css文件里的样式还是正常写，不过class命名一律要用  xxx_xxx  的形式
 * 习惯下命名方式就好，css模块化就不用担心样式表里命名冲突了。
 * 具体使用方式可以参考这里： https://medium.com/nulogy/how-to-use-css-modules-with-create-react-app-9e44bec2b5c2
 *
 * cookie操作依赖universal-cookie
 * 具体使用细节可以参考：https://www.npmjs.com/package/universal-cookie
 *
 */
import React from 'react';
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect
} from 'react-router-dom';
import Cookie from 'universal-cookie';
import Login from './Login';
import Container from './Container';
// import registerServiceWorker from './registerServiceWorker';
import style from './index.css';

const cookies = new Cookie();

class Main extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isLogin: false
		};
	}

	componentWillMount() {
		/*在这里验证登录状态*/
		this.setState({
			isLogin: cookies.get('uid') ? true : false
		})
	}

	loginStatusChange = (e) => {
		this.setState({
			isLogin: e
		})
	}

	render() {
		const isLogin = this.state.isLogin;
		return (
			<Router>
				<div>
					<Route path="/CIM" render={()=>(isLogin ? <Container /> : <Login loginStatusChange={this.loginStatusChange} />)}></Route>
				</div>
			</Router>
		)
	}
}

ReactDOM.render(<Main />, document.getElementById('root'));
// registerServiceWorker();


