/*
* 页面主体之打卡统计
* 
*/
import React, {Component} from 'react';
import StatisticsTable from './StatisticsTable';
import style from './PunchStatistics.css';

const infoContent = {
	'7天': '34%',
	'30天': '44%',
	'arr': [
		{
			'日期': '2018/1/1',
			'学员数(仅会员)': '300',
			'活跃数': '200',
			'活跃比率': '66.67%',
			'打卡数': '100',
			'打卡率': '33.33%',
			'补打卡数': '100',
			'补打卡率': '33.33%'
		},
		{
			'日期': '2018/1/2',
			'学员数(仅会员)': '300',
			'活跃数': '200',
			'活跃比率': '66.67%',
			'打卡数': '100',
			'打卡率': '33.33%',
			'补打卡数': '100',
			'补打卡率': '33.33%'
		}
	]
}

class PunchStatistics extends React.Component{
	constructor(props) {
	  super(props);
	  this.state = {
	  	title: ''
	  };
	}
	componentWillMount(){
		this.setState({
			title1: '近7天(不含今天)平均打卡率:' + infoContent['7天'],
			title2: '近30天(不含今天)平均打卡率:' + infoContent['30天']
		})
	}
	render(){
		return (
			<div>
				<StatisticsTable infoContent={infoContent} infoTitle1={this.state.title1} infoTitle2={this.state.title2}></StatisticsTable>
			</div>
		)
	}
}

export default PunchStatistics


