/*
* 登录组件，cookie存储登录状态
*
*/
import React, {Component} from 'react';
import Cookie from 'universal-cookie';

import style from './Login.css';

const cookies = new Cookie();

class Login extends React.Component{
	constructor(props) {
	  super(props);
	
	  this.state = {
	  	uid: '',
	  	upwd: ''
	  };
	}
	handleLoginClick = () => {
		// 在这里登录，将登录信息发送服务器。
		// 在回掉中将用户token存在cookie，并调用 this.props.loginStatusChange(true) 修改当前登录状态
		if (this.state.uid.length <= 0) {
			return false;
		}
		// cookie有效期为30天
		const cookieTime = new Date(new Date().getTime() + 1000*60*60*24*30);
		cookies.set('uid', this.state.uid, { path: '/',expires: cookieTime });
		this.props.loginStatusChange(true);
	}
	userNameChange = (e) => {
		this.setState({
			uid: e
		})
	}
	passWordChange = (e) => {
		this.setState({
			upwd: e
		})
	}
	render(){
		return (
			<div className={style.login_container}>
				<p>用户登录</p>
				<InputComponent label="账号：" name="username" type="text" handleChange={this.userNameChange} />
				<InputComponent label="密码：" name="password" type="password" handleChange={this.passWordChange} />
				<button onClick={this.handleLoginClick}>登录</button>
			</div>
		)
	}
}

class InputComponent extends React.Component{
	constructor(props) {
	  super(props);
	
	  this.state = {};
	}
	handleChange = (e) => {
		this.props.handleChange(e.target.value);
	}
	render (){
		const _props = this.props;
		return (
			<div>
				<label htmlFor={_props.name}>{_props.label}<input name={_props.name} type={_props.type} onChange={this.handleChange.bind(this)} /></label>
			</div>
		)
	}
}

export default Login


